div#avatar-container {
  position: relative;
  width: 128px;
  height: 320px;
  grid-area: avatar;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
div#avatar-container .avatar-shadow {
  box-shadow: var(--character-shadow);
  background-color: rgb(0 0 0 / 20%);
  width: 60%;
  height: 23px;
  border-radius: 100%;
  margin: -14px auto 0;
}

div#avatar-container #avatar {
  width: 140px;
}

div#cats {
  position: absolute;
  bottom: 10px;
  right: -40px;
  height: 120px;
  transform: rotateY(180deg);
  transition: all 2s ease;
}

div#avatar-container #cats img {
  position: absolute;
}
#ralph {
  width: 120px;
  transform: translateX(600px);
  z-index: 0;
}
#atreyu {
  width: 100px;
  transform: translateX(800px);
  z-index: 1;
  top: 34px;
}

div#avatar-container #player-controls {
  position: absolute;
  bottom: 5px;
  left: 40%;
  transform: translateX(-50%);
  cursor: pointer;
  z-index: 2;
  transition: opacity 0.3s ease;
}

div#avatar-container #player-controls.playing {
  animation: pulse 1s ease-in-out infinite;
}

div#avatar-container #player-controls.paused {
  opacity: 0.7;
}

div#avatar-container #player-controls img {
  width: 80px;
  height: auto;
  display: block;
}

@keyframes pulse {
  0% {
    transform: translateX(-50%) scale(1);
  }
  50% {
    transform: translateX(-50%) scale(1.05);
  }
  100% {
    transform: translateX(-50%) scale(1);
  }
}

/* Game Controller Shortcut */
div#avatar-container #game-controller {
  position: absolute;
  bottom: 5px;
  left: calc(100% + 180px);
  cursor: pointer;
  z-index: 2;
  transition: transform 0.2s ease;
}

div#avatar-container #game-controller:hover {
  transform: scale(1.15);
}

div#avatar-container #game-controller:active {
  transform: scale(0.95);
}

div#avatar-container #game-controller img {
  width: 64px;
  height: auto;
  display: block;
}

/* PC Icon for opening browser */
div#avatar-container #pc-icon {
  position: absolute;
  bottom: 5px;
  left: calc(100% + 240px);
  cursor: pointer;
  z-index: 2;
  transition: transform 0.2s ease;
}

div#avatar-container #pc-icon:hover {
  transform: scale(1.15);
}

div#avatar-container #pc-icon:active {
  transform: scale(0.95);
}

div#avatar-container #pc-icon img {
  width: 112px;
  height: auto;
  display: block;
}

@media screen and (max-width: 640px) {
  div#avatar-container #avatar {
    width: 90%;
  }
  div#cats {
    right: -20px;
    bottom: 0px;
    height: 100px;
  }
  #ralph {
    width: 100px;
  }
  #atreyu {
    width: 80px;
  }
  div#avatar-container #player-controls {
    left: 40px;
    bottom: -10px;
  }
  div#avatar-container #player-controls img {
    width: 60px;
  }
  div#avatar-container #game-controller {
    left: calc(100% + 80px);
    bottom: -10px;
  }
  div#avatar-container #game-controller img {
    width: 50px;
  }
  div#avatar-container #pc-icon {
    left: calc(100% + 120px);
    bottom: -10px;
  }
  div#avatar-container #pc-icon img {
    width: 90px;
  }
}

@media screen and (max-height: 680px) {
  div#avatar-container {
    padding-top: 120px;
  }
  div#avatar-container #player-controls {
    bottom: -10px;
    left: 40px;
  }
  div#avatar-container #game-controller {
    bottom: -10px;
  }
  div#avatar-container #pc-icon {
    bottom: -10px;
  }
}
